.Common_footer {
    background-color: black;
    color: whitesmoke;
    width: 100%;
    height: auto;
  }
  .Row1 {
    width: 100%;
    display: flex;
    justify-content: space-around;
  }
  .Applicaton {
    display: flex;
    border: 2px solid gray;
    justify-content: space-around;
    align-items: center;
    border-radius: 5px;
    margin-top: 20px;
    padding: 12px 8px;
    margin-right: 50px;
    .h4 {
      font-size: 21px;
      margin-bottom: 0 !important;
    }
    .p {
      margin-top: 0 !important;
    }
  }
  
  .Applicaton:hover {
    background-color: whitesmoke;
    color: black;
    cursor: pointer;
    transition: 0.3s ease-out;
  }
  .lists {
    list-style: none;
    margin-top: 20px;
  }
  .lists li {
    padding: 8px 0;
    color: gray;
    cursor: pointer;
  }
  
  .heading {
    margin-top: 30px;
    font-size: 16px;
  }
  .lists li:hover {
    color: wheat;
    transform: translateX(8px);
    transition: all 0.4s ease-in-out;
  }
  .isolate {
    width: 100%;
    opacity: 0.2;
    margin: 10px 0;
    border: 1px solid grey;
  }
  .logo {
    cursor: pointer;
    width: 70px;
    height: 60px;
    margin: 0px 0px;
    color: black;
    margin: 0px 5px;
    border-radius: 5px;
  }
  .footerCompany {
    width: 300x;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .footercopyRights {
    width: 300x;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .Row2 {
    margin-top: 10px;
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: space-around;
  }
  .socialmediaicons {
    width: 300px !important;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: xx-large;
  }
  .icon1:hover {
    cursor: pointer;
    color: rgba(51, 102, 255, 0.7);
    transition: 0.2s ease-in;
    transform: skewY(10deg);
  }
  
  .icon2:hover {
    cursor: pointer;
    color: rgb(10, 161, 221);
    transition: 0.3s ease-in;
    transform: skewX(-10deg);
  }
  
  .icon3:hover {
    cursor: pointer;
    color: rgba(255, 0, 0, 0.623);
    transition: 0.3s ease-in;
    transform: skewY(-10deg);
  }
  .icon4:hover {
    cursor: pointer;
    color: palevioletred;
    transition: 0.3s ease-in;
    transform: skewX(10deg);
  }